body {
    background-color: #2a2a2b;
}
h3, label, small {
    color: #bbb;
}
.DateInput_input {
    font-size:16px;
    padding:5px 25px;
}
.DateRangePickerInput {
    display:flex;
    align-items:center;
    border-radius:4px;
}
.DateRangePickerInput > div {
    width:auto;
}
.main-container {
    display:flex;
    flex-direction: column;
    align-items: stretch;
    height:100vh;
}
.main-container h3 {
    text-align: center;
}
.main-container .chart {
    flex:1;
}
.main-container .source {
    text-align:right;
}
.highcharts-tooltip tspan {
    fill:white;
}